// styles

.bg-dark {color: white;}

.bg-b1 {background-color: $clr-b0;}
.bg-b2 {background-color: $clr-b2;}
.bg-b3 {background-color: $clr-b3;}
.bg-b4 {background-color: $clr-b4;}
.bg-b6 {background-color: $clr-b6;}

.bg-b1,
.bg-b2,
.bg-b3,
.bg-b4,
.bg-b6 {
  background-image:
    linear-gradient(to right, rgba(black,0.5) 0%, rgba(black,0.1) 100%),
    url(../../assets/images/content/patterns/hexagons.svg);
  background-size:
    cover,
    173px auto;
}

.bg-b5 {
  background-color: $clr-4;
  background-image:
    linear-gradient(to right, rgba(white,0.2) 0%, rgba(white,0.2) 100%),
    url(../../assets/images/content/patterns/hexagons.svg);
  background-size:
    cover,
    173px auto;
}

.bg-ci {
  background-color: #4801AF;
  background-image:
    linear-gradient(to right, rgba(#4801AF,0.75) 0%, rgba(#006C9D,0.75) 100%),
    url(../../assets/images/content/patterns/waves.svg);
  background-size: cover;
  background-position: 50%;
}

// strip

div.strip {
  padding: ($spacing*1.5) 0;
  @include bp-m {padding: ($spacing*2) 0;}

  &.section {
    padding: ($spacing*2) 0 ($spacing*1);

    @include bp-m {
      padding: ($spacing*3) 0 ($spacing*2);
      &.latest p.lead span {display: block;}
    }

    div.wrap > h2 {@extend .h1;}

    &[class*="bg-dark"] {
      box-shadow: inset 0 1px 0 0 rgba(white,0.1), inset 0 -1px 0 0 rgba(white,0.1);
      div.wrap > h2 {font-weight: 200;}
      p.lead a {color: white;}
    }
  }

  &.core {@include bp-m-less {padding: 0;}}
  &.alt {padding-top: 0;}

  // newsletter

  &.section.newsletter {
    padding: ($spacing*1.5) 0;
    div.compact-form {margin-bottom: 0;}
    fieldset > p {@include lead();}
  }

  // team

  &.team {
    h3 {@extend .h2;}
  }

  // call to actions

  &.cta {
    position: relative;
    z-index: 50;
    padding: ($spacing*1.5) 0;
    background-color: $clr-1;
    text-align: center;
    color: white;

    @include bp-m {
      text-align: left;

      &.download {padding: $spacing 0;}
    }

    &.client-feedback {
      padding: ($spacing*2) 0;

      div.wrap {
        display: block;

        > h2 {
          position: relative;
          z-index: 1;
          margin-bottom: $vmargin;
          font-size: $fs-30;

          @include bp-m {
            @extend .col-5;
            margin: 0 0 -2rem;
          }

          a {
            color: white;
            text-decoration: none;

            @include bp-m {
              &:hover,
              &:focus {text-decoration: underline;}
            }
          }
        }

        blockquote {
          margin: 0;
          
          @include bp-m {display: flex;}

          div.quote {
            position: relative;
            border-radius: $border-radius;
            padding: $spacing;
            background: rgba(white,0.075);
            text-align: center;

            @include bp-m {
              flex: 1 1 auto;
              order: 2;
            }

            &.size-small {font-size: $fs-18;}
            &.size-medium {font-size: $fs-24; font-weight: 300;}
            &.size-large {font-size: $fs-25; font-weight: 300; @include bp-m { font-weight: 200; font-size: $fs-35;}}

            &:after {
              position: absolute;
              top: 100%; left: 50%;
              border: 10px solid;
              border-color: rgba(white,0.075) transparent transparent rgba(white,0.075);
              content: "";

              @include bp-m {
                top: auto; left: auto; bottom: 0.7rem; right: 100%;
                margin-left: -10px;
                border-color: rgba(white,0.075) rgba(white,0.075) transparent transparent;
              }
            }

            >:last-child {margin-bottom: 0;}

            p {
              &:before {content: "\0201C";}
              &:after {content: "\0201D";}
            }
          }

          div.source {
            margin-top: ($vmargin/2);

            @include bp-m {
              flex: 0 0 auto;
              display: flex;
              align-items: flex-end;
              margin: 0;
              width: 16rem;

              &.download {width: 18rem;}
            }

            span.initials {
              display: inline-block;
              vertical-align: middle;
              margin: 0.75rem 0.75rem 0;
              border-radius: 3rem;
              width: 2.75rem;
              height: 2.75rem;
              line-height: 2.75rem;
              background: rgba(black,0.5);
              text-decoration: none;
              text-align: center;
              font-size: $fs-22;
              font-weight: 300;
              color: white;

              @include bp-m {
                flex: 0 0 auto;
                order: 2;
                margin: 0 1.4rem 0 0.75rem;
              }

              &.s2 {background: $clr-b2;}
              &.s3 {background: $clr-b3;}
              &.s4 {background: $clr-b4;}
            }

            p {
              display: inline-block;
              vertical-align: middle;
              margin: 0.75rem 0 0;
              text-align: center;
              font-size: $fs-16;

              @include bp-s {text-align: left;}

              @include bp-m {
                flex: 1;
                margin: 0;
                text-align: right;
              }

              span {display: block;}
            }
          }
        }
      }
    }

    div.wrap {

      @include bp-m {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      @include bp-l {align-items: flex-end;}

      > h2 {
        @include lead();
        margin: 0;

        @include bp-m {
          flex: 0 1 auto;
          align-self: center;
        }

        > span {
          font-size: $fs-20;
          @include bp-m {display: inline-block;}

          &:before {
            margin: 0 0.4rem 0 0.25rem;
            content: "\02014";
          }         

          span {
            @include bp-s {white-space: nowrap;}
          }
        }

        a {color: white;}
      }

      > p,
      div.audio-insert {
        margin: $vmargin auto 0;

        @include bp-m {
          flex: 0 0 auto;
          align-self: center;
          margin: 0 0 0 $spacing;
        }

        a.button {
          @include bp-s-less {display: block;}
        }

        span.other {
          display: block;
          margin-top: 0.75rem;
          font-size: $fs-18;
          text-align: center;

          a {color: white;}
        }
      }
    }

    @include bp-l {
      &.download,
      &.webinar,
      &.audio {

        body.guide &,
        body.form-response div.page-intro + & {margin-top: ($vmargin*2);}
        
        span.image {
          flex: 0 0 auto;
          display: block;
          position: relative;
          width: 280px;
        
          &:before {
            position: absolute;
            left: -($spacing); bottom: -($spacing*1.5);
            padding-top: 69%;
            width: 100%;
            background: no-repeat 50% 100%;
            background-size: 100% auto;
            content: "";
          }
        }

        div.wrap h2 {flex: 1 1 auto;}
      }

      &.download {
        span.image:before {bottom: -($spacing);}

        a.button {
          @include bp-m {margin: 0.75rem 0;}
        }
      }

      &.gspr-matcher span.image:before {background-image: url(../../assets/images/content/cta-strips/gspr-matcher.png);}
      &.whitepaper-1 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-1.png);}
      &.whitepaper-2 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-2.png);}
      &.whitepaper-3 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-3.png);}
      &.mdready span.image:before {background-image: url(../../assets/images/content/cta-strips/mdready.png);}
      &.webinar span.image:before {background-image: url(../../assets/images/content/cta-strips/webinar-1.png);}
      &.audio span.image:before {background-image: url(../../assets/images/content/cta-strips/non-clinical-physicians.png);}
    }

    &.getting-started {
      padding: ($spacing*2) 0;
      text-align: left;

      @include bp-m {padding: ($spacing*3) 0;}

      div.wrap {
        display: block;

        h2 {
          @extend .h1;
          margin-bottom: $vmargin;
        }

        div.general-wrap {
          @include bp-m {margin-top: ($vmargin*1.5);}
        }

        div.form-box {margin-bottom: 0;}
      }
    }

    &.contact {
      text-align: center;

      div.wrap {display: block;} /* required reset */

      div.col {

        h2 {@include lead();}

        > p {
          @include bp-m {
            margin: 0;

            &.office-times {margin-top: 0.5rem;}
          }
        }
      }
    }

    &.newsletters {
      div.compact-form {
        margin: 0;

        fieldset {
          @include bp-m {
            display: flex;
            align-items: center;
          }

          > p {
            @extend .h3;
            font-weight: 300;

            @include bp-m {
              flex: 0 1 auto;
              align-self: center;
              margin: 0;
              max-width: 60%; // IE 11 bug fix to make the container shrink on smaller screens
            }
          }

          div.form-group {
            margin: 0 auto;
            max-width: 520px;
            
            @include bp-m {
              flex: 1 1 auto;
              margin: 0 0 0 $spacing;
              min-width: 450px;
            }
          }
        }
      }
    }
  }
}

// page intro

div.page-intro {
  position: relative;
  padding: ($spacing*4) 0 ($spacing*2);
  text-align: center;

  &.bg-dark {
    &:after {
      position: absolute;
      top: 0; bottom: 0; left: 0; right: 0;
      z-index: 2;
      background-image: linear-gradient(to top, rgba(black,0.3) 0%, transparent 200px, transparent 0%);
      content: "";
    }
  }

  body.analytics & {display: none;}

  @include bp-m {
    padding: ($spacing*5.5) 0 ($spacing*2.5);
    body.guide & {padding-bottom: $spacing*3;}
  }

  body.article & {
    border-bottom: 1px solid white;
    background-position: 50%;
    background-size: cover;

    &:before {
      @extend .bg-b3;
      position: absolute;
      top: 0; left: 0; bottom: 0; right: 0;
      z-index: 1;
      content: "";
      opacity: 0.8;
    }
  }

  &.alternative {
    background-position: 100% 50%;
    background-size: cover;

    @include bp-m {background-position: 50%;}

    &:before {
      position: absolute;
      top: 0; left: 0; bottom: 0; right: 0;
      z-index: 1;
      content: "";
      opacity: 0.85;

      body.services & {@extend .bg-b2;}
      body.toolkit & {@extend .bg-b6;}
    }
  }

  div.wrap {
    position: relative;
    z-index: 3;
  }

  h1 {
    margin-bottom: 0;
    @include bp-l {font-size: $fs-70;}

    body.guide:not([class*="index"]) & {@extend .h1;}

    body.error & {
      @extend .h2;
      span {
        display: block;
        font-size: $fs-70;
        @include bp-m {font-size: $fs-100;}
      }
    }

    &.toolkit {
      span.heading {
        display: block;

        @include bp-l {
          > span {display: block;}
        }
        
        body.toolkit-home & {
          font-size: $fs-45;
          @include bp-m {font-size: $fs-75;}
          @include bp-l {font-size: $fs-100;}

          b {font-weight: 400;}

          sup {
            font-weight: 400;
            font-size: $fs-22;
            line-height: 2;
            vertical-align: top;
          }
        }
      }
    }
  }

  p {
    margin: $vmargin 0 0;

    &.lead {
      @include bp-m {
        margin-left: auto;
        margin-right: auto;
        max-width: 800px;
      }

      body.toolkit-home & {
        @include bp-m {font-size: $fs-35; font-weight: 200;}
        @include bp-l {font-size: $fs-40;}
      }

      body.form-response & {
        display: inline-block;
        border-radius: $border-radius;
        padding: $spacing ($spacing*2);
        max-width: none;
        background: $clr-4;
      }

      span {@include bp-m {display: block;}}
    }

    &.phone-number-cta {
      margin-top: ($vmargin*2);

      span.phone-lead {
        @include lead();
        display: block;
        margin-bottom: ($vmargin/2);
      }

      span.office-times,
      span.extra {
        display: block;
        margin-top: 0.5rem;
        font-size: $fs-16;

        a {color: white;}
      }
    }
  }

  &.bg-dark {
    p.lead a {color: white;}

    blockquote {
      p.quote {
        background: rgba(black,0.25);
        &:after {border-color: rgba(black,0.25) transparent transparent rgba(black,0.25);}
      }

      p.source img {box-shadow: 0 0 0 6px rgba(black,0.25);}
    }
  }
      
  ul.buttons {
    margin: $vmargin auto 0;
  }

  blockquote {
    position: relative;
    margin: $vmargin auto 0;
    
    @include bp-m {display: inline-block;}

    div.home-intro & {
      @include bp-l {
        //display: inline-block;
        margin: 0 0 ($vmargin*2);
      }
    }

    p.quote {
      @include lead();
      position: relative;
      margin: 0;
      border-radius: $border-radius;
      padding: $spacing;
      background: rgba($clr-2,0.075);

      @include bp-l {font-size: $fs-30;}

      &:after {
        position: absolute;
        top: 100%; left: 50%;
        border: 10px solid;
        border-color: rgba($clr-2,0.075) transparent transparent rgba($clr-2,0.075);
        content: "";

        @include bp-l {
          left: auto; top: 50%; left: 100%;
          margin-right: -10px;
        }
      }

      > span {
        display: inline-block;
        span {@include bp-m {display: block;}}
      }
    }

    p.source {
      margin-left: auto;
      margin-right: auto;
      width: 4em;
      height: 4em;

      @include bp-l {
        position: absolute;
        left: 100%; top: 50%;
        margin: -2em 0 0 30px;
      }

      @include bp-l {font-size: $fs-25;}

      img {
        display: block;
        overflow: hidden;
        border-radius: 50%;
        width: 100%;      
        transform: scaleX(-1);
        box-shadow: 0 0 0 6px rgba($clr-2,0.075);
      }
    }
  }
}

// home intro

div.home-intro {
  input#question-funnel-1:checked ~ div.page-intro {@extend .bg-b2;}
  input#question-funnel-2:checked ~ div.page-intro {@extend .bg-b6;}
  input#question-funnel-3:checked ~ div.page-intro {@extend .bg-b3;}

  div.page-intro {
    position: relative;
    padding-bottom: 0;
    //transition: background 1s 0s;

    div.wrap {
      position: relative;
      z-index: 3;
    }

    h1 {
      position: relative;
      z-index: 10;
      margin-bottom: $vmargin;

      @include bp-s {margin-bottom: $vmargin*1.5;}
      @include bp-m {margin-bottom: $vmargin*2;}
      @include bp-l {margin-bottom: $vmargin*1.5;}

      span {display: block;}
    }
  }
}

// question funnels - home-intro

div.question-funnels {
  
  input[type="radio"] {
    @include hide-input();
    @include bp-l-less {display: none;}
  }

  input#question-funnel-1:checked ~ div.page-intro div.question-funnels-options ul li label[for="question-funnel-1"],
  input#question-funnel-2:checked ~ div.page-intro div.question-funnels-options ul li label[for="question-funnel-2"],
  input#question-funnel-3:checked ~ div.page-intro div.question-funnels-options ul li label[for="question-funnel-3"] {
    margin-top: 0;
    padding-top: ($spacing*1.5);
    background: white;
    color: $clr-1;
    box-shadow: none;

    @include bp-xl {padding-top: ($spacing*2);}

    &:before {display: block;}
  }

  input#question-funnel-1:checked ~ div.page-intro ~ div.question-funnels-content div.question-funnel-1,
  input#question-funnel-2:checked ~ div.page-intro ~ div.question-funnels-content div.question-funnel-2,
  input#question-funnel-3:checked ~ div.page-intro ~ div.question-funnels-content div.question-funnel-3 {display: block;}

  // page-intro

  div.page-intro {
    padding-bottom: ($spacing*2);
    @include bp-l {padding-bottom: 0;}
  }

  // options

  div.question-funnels-options {
    // overflow-x: scroll;
    // margin: 0 (-$gutter);
    display: none;

    @include bp-l {
      display: block;
      overflow: visible;
      margin: 0;
    }

    ul {
      display: flex;
      margin: 0;
      //min-width: 768px;
      
      li {
        display: flex;
        align-items: stretch;
        flex: 1;

        label {
          position: relative;
          display: block;
          margin: 0 2px;
          margin-top: ($spacing/2);
          padding: $spacing;
          padding-bottom: ($spacing*1.5);
          background-color: $clr-1;
          background-image: linear-gradient(to bottom, transparent 0%, rgba(black,0.2) 96%, rgba(black,0.4) 100%);
          font-size: $fs-22;
          font-weight: 300;
          cursor: pointer;
          outline: none;
          user-select: none;
          -webkit-user-select: none;
          box-shadow: 0 -1px 4px 0 rgba(black,0.3);

          @include bp-l {border-radius: $border-radius $border-radius 0 0;}

          &.question-funnel-1 {
            background-color: $clr-b2;
            @include bp-l {&:hover {background-color: lighten($clr-b2, 3%);}}
          }

          &.question-funnel-2 {
            background-color: $clr-b6;
            @include bp-l {&:hover {background-color: lighten($clr-b6, 3%);}}
            
          }

          &.question-funnel-3 {
            background-color: $clr-b3;
            @include bp-l {&:hover {background-color: lighten($clr-b3, 3%);}}
          }

          @include bp-xl {
            padding: ($spacing*1.5) $spacing;
            font-size: $fs-25;
          }

          &:before {
            display: none;
            @extend .icon-extension, .icon-angle-down;
            position: absolute;
            left: 0; right: 0; bottom: 0;
            background: white;
            text-align: center;
            font-size: $fs-20;
            color: $clr-2;
          }
        }

        &:first-child label {margin-left: 0;}
        &:last-child label {margin-right: 0;}
      }
    }
  }

  // content

  div.question-funnels-content {
    background: white;
    text-align: left;

    div.question-funnel {
      display: none;

      @include bp-l-less {
        &.question-funnel-1,
        &.question-funnel-2,
        &.question-funnel-3 {display: block!important;}

        h2 {text-align: center;}
      }
    }
  }

  div.contact,
  div.client-feedback {
    @include bp-l {display: none;}
  }
}

// associates

body.associates div.features-intro {
  @include bp-m-less {padding-top: 0;}
}

// sale content

div.sale-content {
  h3 {@extend .h2;}
}

// article pages

div.article-content {
  > p:nth-of-type(1):not([class]) {@include lead();}
  > ul {@extend .disc;}
  > ol {@extend .decimal;}
}

// guide pages

div.guide-content {
  background: white;

  @include bp-m {
    position: relative;
    z-index: 2;
    margin: -1.5rem 0 0;
    padding: 0 0 $vmargin;
    background: transparent;
  }

  div.aesthetic {
    @include bp-m {
      border-radius: 0.25rem;
      padding: ($spacing*2);
      background: white;
      box-shadow: 0 0 4px 2px rgba(black,0.1);
    }

    > ul:not([class]) {@extend .disc;}
    > ol:not([class]) {@extend .decimal;}
    > :last-child {margin-bottom: 0;}

    @include bp-m-less {
      p.back-top {@include hide-screen();}
    }
  }
}

// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  &.spacer-up + p {@extend .spacer-up;}

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}
    }
  }
}

// announcement

div.announcement {
  @extend .bg-dark;
  padding: $spacing 0;
  background: $clr-b0;
  text-align: center;

  div.wrap > :last-child {margin-bottom: 0;}

  span.text {
    display: block;
    @include bp-l {display: inline-block;}
  }
  .button {
    margin-top: ($vmargin/2);
    @include bp-l {margin: 0 0 0 ($vmargin/2);}
  }
}

// promotions

div.promotion {
  padding: ($spacing/1.5) 0;
  background: $clr-b1;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {color: white;}
}

// notices

p.notice {
  position: relative;
  border: 3px solid $clr-4;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;

  &:before {
    position: absolute;
    left: 0.8rem; top: 1.05rem;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution,
  &.info {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-b1;
    background: rgba($clr-b1,0.05);
    text-align: center;

    &:before {content: none;}

    div.buy-details & {
      border-color: white;
      background: rgba(white,0.1);

      span {
        display: block;
        font-size: $fs-18;
      }

      b {font-size: $fs-22;}
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
    }
  }

  &.info {
    &:before {
      @extend .icon-extension,.icon-info-circle;
    }
  }
}

// contact form inline

input#contact-form-inline {
  @include hide-input();

  &:checked {
    + div.row {
      label.button span[class*="icon-"]:before {@extend .icon-minus-circle;}
      + div.contact-form-inline {display: block;}
    }
  }
}

div.contact-form-inline {
  display: none;
  margin: 0 auto;
  border-radius: $border-radius;
  padding: $spacing 0;
