// supporting info - on guide and article pages

div.supporting-info {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;

  &.start {
    margin-bottom: $vmargin;
    border-bottom: 3px solid $clr-4;
    padding: 0.5rem 0;
    text-align: center;

    @include bp-m-less {display: block;}
  }

  ul, p {margin: 0.25rem 0;}

  &.end {
    margin-bottom: $vmargin;
    border-radius: $border-radius;
    padding: 0.5rem 1rem;
    background: $clr-3;

    div.guide-nav-end + & {
      border-radius: 0 0 $border-radius $border-radius;
    }

    ul.share-options {flex: 1 1 auto;}
  }

  div.meta {
    flex: 1 0 auto;
    margin-bottom: 0.5rem;

    @include bp-m {
      display: flex;
      align-items: center;
      margin-bottom: 0;
    }

    a.author-linkedin {
      flex: 0 1 auto;
      display: block;
      margin: 0 auto 0.5rem;
      font-size: $fs-30;

      @include bp-m {margin: 0 0.5rem 0 0;}
    }
  }

  ul.share-options {flex: 0 0 auto;}

  ul.general {
    list-style: none;
    font-size: $fs-18;

    @include bp-m {margin-right: 1rem;}

    li[class*="icon-"] {
      display: flex;
      align-items: center;
      justify-content: center;

      @include bp-m {justify-content: left;}

      &:before {margin-right: 0.25rem;}

      a {
        text-decoration: none;
        color: $clr-1;

        @include bp-m {
          &:hover,
          &:focus {color: $clr-b1;}
        }
      }
    }
  }

  p.top {
    flex: 0 0 auto;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 2rem;
      width: 1.7rem;
      height: 1.7rem;
      background: $clr-b1;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      @include bp-m {
        &:hover,
        &:focus {
          box-shadow: inset 0 0 0 4rem rgba(black,0.1);
        }
      }

      span {@include hide-screen();}
    }
