// testimonials

div.testimonials {
  margin: $vmargin 0;

  ul {
    margin-bottom: 0;
    text-align: center;

    li {

      input {
        @include hide-input();

        &:checked {
          + div.content {
            overflow: visible;
            //visibility: visible;
            margin-left: 0;
            padding: 1.25rem 0;
            width: 100%;
            max-height: 30rem;
            opacity: 1;
            transition: opacity 0.25s 0.25s, margin-left 0.25s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

            @include bp-m {padding: 3rem 0;}
          }
        }
      }

      div.content {
        display: flex;
        justify-content: center;
        overflow: hidden;
        //visibility: hidden; - don't add this because it causes Google Lighthouse to complain about hidden labels
        margin-left: -20rem;
        padding: 0;
        width: 0;
        max-height: 0;
        opacity: 0;
        transition: opacity 0.25s, margin-left 0.25s, visibility 0s 0.25s, width 0s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

        blockquote {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-bottom: 0;
          border-left: 5px solid $clr-b1;
          border-radius: $border-radius;
          padding: $spacing ($spacing/1.7);
          max-width: 750px;
          min-height: 18rem;
          background: $clr-4;

          .bg-b5 & {background: rgba($clr-1,0.05);}

          @include bp-s {
            padding: $spacing;
            min-height: 22rem;
          }

          @include bp-m {padding: $spacing ($spacing*1.5);}

          div.quote {
            font-size: $fs-18;
            @include bp-m {font-size: $fs-20;}

            &.small {
              @include bp-s-less {
                p:last-child {@include hide-screen();}
              }
            }

            &.medium {
              font-size: $fs-22;
              @include bp-m {font-size: $fs-25;}
            }
             
            &.large {
              font-size: $fs-25;
              font-weight: 300;

              @include bp-s {font-size: $fs-30;}

              @include bp-m {
                font-size: $fs-40;
                font-weight: 200;
              }
            }
              
            p {
              &:before {content: "\0201C";}
              &:after {content: "\0201D";}
            }
          }

          p.source {
            margin-bottom: 0;
            font-style: normal;
            font-weight: 700;
            @include bp-m-less {font-size: $fs-18;}
            span {display: block;}
          }
        }

        label.button {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          right: 50%; top: 100%;
          z-index: 10;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 3rem;
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          font-size: $fs-25;

          @include bp-m {
            right: 0; top: 50%;
            margin: -1.25rem -1.25rem 0 0;
          }

          &:before {
            @extend .icon-extension, .icon-angle-right;
          }

          span {@include hide-label();}
        }
      }
    }
  }
}

// alternative contact - talk + support pages

div.alternative-contact {
  margin: 0 auto;
  text-align: center;

  @include bp-m {margin-top: ($vmargin*1.5);}

  h2 {
    @extend .h3;
    margin-bottom: ($vmargin/2);
  }

  p {
    margin-bottom: 0;
    @include bp-m {span {display: block;}}
  }
}

// question tool

$toolHeight: 80rem;
$toolAnimation: 0.2s;

div.question-tool {
  margin-bottom: $vmargin;
  text-align: center;

  @include bp-m {
    body.services & {margin: ($vmargin*2.5) 0 ($vmargin*1.5);}
  }

  input[type="radio"] {
    @include hide-input();
  }

  div.question-tool-start {overflow: hidden;}
      
  div.question-content,
  div.answer-content {
    overflow: hidden;
    max-height: 0;
    opacity: 0;
  }

  input#question-tool-start:checked,
  input[id*="question-yes"]:checked + input,
  input[id*="question-no"]:checked {
    + div.question-tool-start,
    + div.question-content {
      max-height: 0!important;
      opacity: 0!important;
      transition: opacity $toolAnimation ($toolAnimation*2), max-height 0s ($toolAnimation*4)!important;

      + input + input + div[class*="question-content"],
      + div.answer-content {
        max-height: $toolHeight;
        opacity: 1;
        transition: opacity $toolAnimation ($toolAnimation*4), max-height 0s ($toolAnimation*4); 
      }
    }
  }

  input[id*="last-question-input"]:checked ~ div.question-content + div.answer-content {
    max-height: $toolHeight;
    opacity: 1;
  }

  input[id*="question-yes"]:checked ~ div.answer-content {
    .answer-content-yes {display: block;}
    .answer-content-no {display: none;}
    h3.answer-content-yes {
      display: block;
      @include bp-m {display: flex;}
    }
  }

  // styling

  input[id*="question-yes"]:checked + input + div.question-content ul li label[for*="question-yes"],
  input[id*="question-no"]:checked + div.question-content ul li label[for*="question-no"] {
    background: $clr-b1;
    color: white;
    span.radio {border-color: $clr-b1;}
    span.radio:before {background: $clr-2; content: "";} // fill radio button
  }

  // question tool start

  div.question-tool-start {
    margin: 0;
    max-height: $toolHeight;

    @include bp-m {
      p.lead span {display: block;}
    }

    h2 {
      @extend .bg-b3;
      margin-bottom: 0;
      border-radius: $border-radius $border-radius 0 0;
      padding: 1rem;
      color: white;
    }

    div.text {
      border-radius: 0 0 $border-radius $border-radius;
      padding: 1rem;
      background: $clr-4;
      text-align: center;

      div.strip.bg-b5 & {background: rgba($clr-2,0.075);}

      @include bp-m {
        padding: 2rem;
      }

      > :last-child {margin-bottom: 0;}
    }
  }

  // question content

  div.question-content {

    h3 {
      @extend .h2;
      overflow: hidden;
      position: relative;
      margin-bottom: 4px;

      span.number { 
        @extend .bg-b3;
        display: block;
        border-radius: $border-radius $border-radius 0 0;
        padding: 0.75rem;
        color: white;

        @include bp-m {padding: 1rem;}
      }

      span.text {
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 1rem;
        min-height: 12.5rem;
        background: $clr-4;
        font-size: $fs-20;

        @include bp-s {
          padding: 1rem 2rem;
          min-height: 8.5rem;
        }

        @include bp-m {
          min-height: 7.5rem;
          font-size: $fs-25;
        }

        div.strip.bg-b5 & {background: rgba($clr-2,0.075);}
      }
    }

    ul {
      display: flex;
      margin: 0;

      li {
        flex: 1;
        border-top-width: 4px;
        width: 50%;

        label {
          display: flex;
          justify-content: center;
          padding: 1.05rem; // unusal padding match height of div.question-tool-start
          background: $clr-4;
          font-size: $fs-22;
          color: $clr-b0;
          cursor: pointer;
          user-select: none;
          -webkit-user-select: none;
          
          div.strip.bg-b5 & {background: rgba($clr-2,0.125);}

          @include bp-m {
            font-size: $fs-25;

            &:hover {
              background: $clr-3;
              div[class*="bg-b"] & {background: rgba(white,0.7);}
              span.radio {border-color: $clr-b0;}
            }
          }

          span {
            line-height: 1;
            &.radio {border-width: 2px;}
          }
        }

        &:first-child label {
          margin-right: 2px;
          border-radius: 0 0 0 $border-radius;
        }

        &:last-child label {
          margin-left: 2px;
          border-radius: 0 0 $border-radius 0;
        }
      }
    }
  }

  // answer content

  div.answer-content {

    .answer-content-yes {display: none;} // default hiding

    h3 {
      @extend .bg-b5;
      margin-bottom: 0;
      border-radius: $border-radius $border-radius 0 0;
      padding: 1rem;
      background-color: $clr-success;
      color: white;

      @include bp-m {
        display: flex;
        justify-content: center;
        text-align: left;
        @-moz-document url-prefix() {align-items: center;}
      }

      &.answer-content-yes {
        background-color: $clr-warning;
        color: $clr-1;

        &:before {
          @extend .icon-exclamation-circle;
        }
      }

      &:before {
        @extend .icon-extension, .icon-check-circle;
        font-size: $fs-40;
        @include bp-m {margin-right: 0.5rem;}
      }

      span {
        display: block;
        strong {
          text-decoration: underline;
          text-decoration-thickness: 4px;
          text-underline-offset: 3px;

          @include bp-s-less {white-space: nowrap;}
        }
      }
    }

    div.text {
      border-radius: 0 0 $border-radius $border-radius;
      padding: 1rem;
      background: $clr-4;
      text-align: left;

      div.strip.bg-b5 & {background: rgba($clr-2,0.075);}

      @include bp-m {
        padding: 2rem;
        div.col > div > :last-child {margin-bottom: 0;}

        div.answer-content-yes p {
          font-size: $fs-22;
          font-weight: 300;
        }
      }

      div.form-group {margin-top: 0;}
      button {width: 100%;}
    }
  }
}

// article categories

div.article-categories {
  display: block;
  position: relative;
  z-index: 2;
  border-bottom: 4px solid white;
  padding: 0.75rem;
  background: $clr-4;
  font-size: $fs-16;

  @include bp-l {display: flex;}

  h2 {
    @extend .h5;
    flex: 1 1 auto;
    margin: 0 0 0 0.25rem;
  }

  div.select-toggle {
    display: none;
    overflow: hidden;
    position: relative;
    border-radius: 0.25rem;
    background: $clr-3;

    @include bp-l {
      display: flex;
      justify-content: flex-end;
      flex: 0 1 auto;
    }
  }

  // open

  input#category-toggle {
    @include hide-input();

    &:checked {
      + label.category-toggle {
        @extend .icon-extension, .icon-angle-right;
        position: static;
        padding: 0.25rem 0.5rem;
        background: $clr-2;
        line-height: $lh-general;
        color: white;

        @include bp-m {
          &:hover {background: $clr-b1;}
        }

        span {@include hide-screen();}
    
        + ul {
          &:before {content: none;}

          li {
            position: static;
            flex: 1 1 auto;
            box-shadow: 1px 0 0 $clr-4;
          }
        }
      }
    }
  }

  // closed

  label.category-toggle {
    display: block;
    position: absolute;
    left: 0; right: 0; top: 0; bottom: 0;
    z-index: 2;
    cursor: pointer;

    span {@include hide-screen();}

    @include bp-m {
      &:hover + ul:before {background: $clr-b1;}
    }
  }

  ul {
    position: relative;
    z-index: 1;
    display: flex;
    margin: 0;

    &:before {
      display: block;
      padding: 0.25rem 0.5rem;
      font-weight: 700;
      background: $clr-2;
      color: white;
      content: "Filter by";
    }

    li {
      @include hide-label();
      margin: 0;

      label {
        display: block;
        padding: 0.25rem 0.5rem;
        cursor: pointer;
        animation: preventPointerEvents 1s ease forwards;
        pointer-events: none;
        user-select: none;
        -webkit-user-select: none;

        @include bp-m {
          &:hover {background: white;}
        }
      }
    }
  }
}

input.category-input {@include hide-input();}

input#tag-all:checked ~ div.general-wrap div.article-categories li.tag-all,
input#tag-1:checked ~ div.general-wrap div.article-categories li.tag-1,
input#tag-2:checked ~ div.general-wrap div.article-categories li.tag-2,
input#tag-3:checked ~ div.general-wrap div.article-categories li.tag-3,
input#tag-4:checked ~ div.general-wrap div.article-categories li.tag-4,
input#tag-5:checked ~ div.general-wrap div.article-categories li.tag-5,
input#tag-6:checked ~ div.general-wrap div.article-categories li.tag-6,
input#tag-7:checked ~ div.general-wrap div.article-categories li.tag-7,
input#tag-8:checked ~ div.general-wrap div.article-categories li.tag-8,
input#tag-9:checked ~ div.general-wrap div.article-categories li.tag-9 {
  position: static;
  label {background: white;}
}

input.category-input ~ div.general-wrap ol.articles li {display: none;}

input#tag-all:checked ~ div.general-wrap ol.articles li,
input#tag-1:checked ~ div.general-wrap ol.articles li.tag-1,
input#tag-2:checked ~ div.general-wrap ol.articles li.tag-2,
input#tag-3:checked ~ div.general-wrap ol.articles li.tag-3,
input#tag-4:checked ~ div.general-wrap ol.articles li.tag-4,
input#tag-5:checked ~ div.general-wrap ol.articles li.tag-5,
input#tag-6:checked ~ div.general-wrap ol.articles li.tag-6,
input#tag-7:checked ~ div.general-wrap ol.articles li.tag-7,
input#tag-8:checked ~ div.general-wrap ol.articles li.tag-8,
input#tag-9:checked ~ div.general-wrap ol.articles li.tag-9 {display: flex;}

// article funnels

div.article-funnels.general-wrap {
  margin-top: $vmargin;

  > h2 {
    @extend .h5;
    position: relative;
    z-index: 2;
    margin: 0;
    border-bottom: 4px solid white;
    padding: ($spacing/2) $spacing;
    background: $clr-4;
  }

  p.more {
    margin: 0;
    border-top: 4px solid white;
    padding: 0.75rem;
    background: $clr-3;
    text-align: center;
  }
}
