// unstyled list

.unstyled {
  margin-left: 0;
  list-style: none;
  li {margin: 0.5rem 0;}
}

// spaced list

.spaced {
  li {margin: 0.5rem 0;}
}

// disc list

.disc {
  margin-left: 1.25rem;
  list-style: disc;

  ul {
    margin-left: 1.25rem;
    list-style: circle;
  }
}

// decimal list

.decimal {
  margin-left: 1.25rem;
  list-style: decimal;
}

// lower alpha list

.lower-alpha li {
  margin-left: 1.25rem;
  list-style: lower-alpha;

  ol {margin-bottom: 0;}
}

// checked alternate

ul.checked-alternate {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  border-radius: $border-radius;
  background: $clr-4;
  text-align: left;
  font-size: $fs-18;

  @include bp-m {margin: 0;}

  li {
    position: relative;
    //border-top: 4px solid white;
    padding: 0.6rem 2rem;
    padding-left: 2.5rem;
    
    &:first-child {border-top: 0;}
    &:nth-child(odd) {background: rgba(white,0.4);}

    &:before {
      @extend .icon-extension,.icon-check-circle;
      position: absolute;
      left: 0.8rem; top: 0.65rem;
      @-moz-document url-prefix() {top: 0.55rem;}
      font-size: $fs-25;
      color: $clr-success;
      .bg-dark & {color: white;}
    }
  }
}

// checked list

ul.checked {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin: 0.5rem 0;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      position: absolute;
      left: 0; top: 0.3rem;
      @-moz-document url-prefix() {top: 0.2rem;}
      color: $clr-success;
      .bg-dark & {color: white;}
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }

  div.product-overview & {
    text-align: left;
    font-size: $fs-20;
  }
}

// asterisk list

ul.asterisk {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.3rem;

      @-moz-document url-prefix() {top: 0.2rem;}

      body.services & {color: $clr-b2;}
      body.academy &,
      body.associates &,
      ol.events & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// chevroned list

ul.chevroned {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1rem;

    &:before {
      @extend .icon-extension,.icon-angle-right;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;

      body.services & {color: $clr-b2;}
      body.academy & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// icon list

ul.icons {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-20;

      body.services & {color: $clr-b2;}
      body.academy & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
    }
  }
}

// styled list

ul.styled {
  border-radius: $border-radius;
  padding: ($spacing*0.5) $spacing;
  background: $clr-4;

  @include bp-m {font-size: $fs-22;}

  .bg-dark &,
  .bg-b5 & {
    background: rgba(black,0.2);
    li:before {color: white!important;}
  }

  //li {margin: 0.5rem 0!important;}
}

// buttons

ul.buttons {
  text-align: center;

  &.inline {
    @include bp-m {
      display: flex;
      justify-content: center;
    }
  }

  div.service-process div.text & {margin-bottom: 0;}

  li {
    display: inline-block;
    margin: 0.5rem 0;
    
    @include bp-m {
      flex: 0 1 auto;
      margin: 0.5rem 0.25rem;
    }
  }
}

// questions

ul.questions {
  @include lead();

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.2rem;
    }
  }
}

// hierarchy links

div.hierarchy {
  padding: 0.4rem 0;
  background: $clr-3;

  @include bp-s-less {@include hide-screen();}

  ol {
    margin: 0;
    font-size: $fs-16;

    @include bp-s {
      display: flex;
      align-items: baseline;
    }

    li {
      margin-right: 0.35rem;

      &:before {
        @extend .icon-extension, .icon-angle-left;
        flex: 0 1 auto;
        margin-right: 0.25rem;
        font-size: $fs-10;
      }
    
      a,
      > span {
        flex: 0 1 auto;
        text-decoration: none;
        color: #3B3D41;
      }
      
      a {
        @include bp-m {
          &:hover,
          &:focus {
            text-decoration: underline;
            color: $clr-b1;
          }
        }
      }
    }
  }
}

// share options

ul.share-options {
  margin-bottom: 0;
  font-size: $fs-20;

  li {
    display: inline-block;
    margin-right: 0.25rem;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3rem;
      width: 1.7rem;
      height: 1.7rem;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      &.icon-linkedin {background: #0077B5;}
      &.icon-twitter {background: #55ADEE;}
      &.icon-facebook {background: #4267B2;}
      &.icon-envelope {background: #7D7D7D;}

      @include bp-m {
        &:hover,
        &:focus {box-shadow: inset 0 0 0 4rem rgba(black,0.1);}
      }

      span {@include hide-screen();}
    }

    &:last-child {margin-right: 0;}
  }
}

// brand assets

ul.brand-assets {
  border-top: 1px solid $clr-3;

  li {
    border-bottom: 1px solid $clr-3;
    padding: 0.4rem 0;

    a {
      text-decoration: none;
      @include bp-m {
        &:hover,
        &:focus {text-decoration: underline;}
      }
    }
  }
}

// videos

ol.videos li.col {flex: 0 1 auto;}

// document list

ul.documents {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-file;
      position: absolute;
      left: 0; top: 1px;
      font-size: $fs-24;
    }
  }
}

// numbered discs

ol.numbered-discs {
  margin-left: 0!important;
  list-style: none!important;
  counter-reset: numbered-discs;

  li {
    position: relative;
    margin-bottom: 0.5rem;
    padding-left: 2rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      margin-right: 1rem;
      border-radius: 50%;
      padding: 0;
      width: 1.3rem;
      height: 1.3rem;
      line-height: 1.4rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 700;
      font-size: $fs-16;
      color: white;
      counter-increment: numbered-discs;
      content: counter(numbered-discs);

      body.services & {background: $clr-b2;}
      body.resources & {background: $clr-b3;}
      body.academy & {background: $clr-b4;}
    }
  }
}

// ordered blocks

ol.ordered-blocks {
  margin-top: ($vmargin*2);
  counter-reset: numbered-blocks;
  text-align: center;

  @include bp-m {
    margin-top: 0;
    text-align: left;
  }

  &.checks li:before {
    @extend .icon-extension,.icon-check;
  }

  li {
    position: relative;
    margin-bottom: 4px;
    padding: $spacing;
    background: $clr-4;

    &:first-child {border-radius: $border-radius $border-radius 0 0;}
    &:last-child {border-radius: 0 0 $border-radius $border-radius;}

    @include bp-m {
      padding: ($spacing/1.25) $spacing;
      padding-left: ($spacing*1.75);
    }

    &:before {
      position: absolute;
      right: 50%; top: 0;
      margin: -1rem -1rem 0 0;
      border-radius: 50%;
      width: 2rem;
      height: 2rem;
      line-height: 2.2!important;
      background: $clr-b1;
      text-align: center;
      font-weight: 700;
      font-size: $fs-18;
      color: white;
      counter-increment: numbered-blocks;
      content: counter(numbered-blocks);
      box-sizing: border-box;
      box-shadow: 0 0 0 4px white;

      @-moz-document url-prefix() {padding-top: 0.4rem;}

      body.services & {background: $clr-b2;}
      body.resources & {background: $clr-b3;}
      body.associates & {background: $clr-b4;}
      body.toolkit & {background: $clr-b6;}

      @include bp-m {right: 100%; top: 50%;}
    }

    > :last-child {margin-bottom: 0;}
  }
}

// product assets

ul.product-assets {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  border-radius: 0.25rem;
  padding: 0.2rem 0.5rem;
  background: rgba(white,0.075);
  
  div.product-overview & {background: $clr-4;}

  ol.product-funnels & {
    margin: 0.7rem 0 0;
    font-size: $fs-16;
    li {margin: 0 0.25rem;}
  }

  li {
    display: flex;
    align-items: center;
    flex: 0 0 auto;
    margin: 0 0.5rem;
    
    &:before {margin-right: 0.2rem;}
  }
}

// product funnels

ol.product-funnels {

  div.toolkit-checkout & {
    @include bp-m {
      div.content {
        border-radius: $border-radius;
        overflow: hidden;
      }
    }
  }

  > li {
    a.content {
      @include bp-m {
        &:hover,
        &:focus {background: rgba($clr-2,0.125);}
      }

      span.resource,
      ul.product-assets,
      div.text,
      span.frequency,
      span.vat,
      span.info {color: $clr-1;}
    }

    .content {
      display: block;
      text-decoration: none;
      transition: background-color 0.15s;

      &:not([class*="bg-dark"],[class*="bg-b5"]) {background: rgba($clr-2,0.05);}

      @include bp-m {
        display: flex;
        flex-direction: column;
        flex: 1;
      }

      img {
        display: block;
        width: 100%;
      }

      div.header {
        padding: $spacing 0.5rem;
        text-align: center;

        &:not([class*="bg-dark"],[class*="bg-b5"]) {background: rgba($clr-2,0.075);}

        div.toolkit-products & {padding-bottom: ($spacing*0.75);}

        h3 {
          margin: 0;
          //span.category span:last-child {color: white;}

          div.toolkit-categories & {
            font-size: 1.7em;
            font-weight: 400;
            span.category {margin-bottom: 0;}
          }

          div.toolkit-products &,
          div.white-papers & {@extend .h4;}

          span.resource {
            display: block;
            font-size: $fs-18;
            font-weight: 400;
          }
        }
      }

      &.bg-dark div.header {background: rgba(black,0.2);}
      &.bg-b5 div.header {background: rgba(black,0.075);}  
      
      div.text {
        flex: 1;
        padding: $spacing;
        text-align: center;
        
        body.associates & {text-align: left;}

        > :last-child {margin-bottom: 0;}

        ul {
          margin-bottom: 0;
          font-size: $fs-16;
        }

        p {
          font-size: $fs-18;
          &.description {font-size: $fs-22;}
          div.white-papers &,
          body.toolkit-home & {margin-bottom: 0;}

          &.description-lead:after {content: ".";}
        }

        @include bp-m {
          flex: 1;
          span.button {display: none;}
        }
      }

      div.cta {
        flex: 0 1 auto;
        padding: $spacing;
        padding-top: 0;
        text-align: center;
        font-size: $fs-18;

        &.lone {padding-top: $spacing;}

        > :last-child {margin: ($vmargin/1.5) 0 0;}

        p.prices {
          margin-bottom: 0;
          font-size: $fs-18;
          text-align: center;

          span.info,
          span.vat {
            display: block;
            font-size: $fs-15;
            opacity: 0.9;
          }

          span.price {
            position: relative;
            display: inline-block;

            span.amount {
              font-size: $fs-30;
              line-height: 1.1;
              &.checkout {font-size: $fs-40;}
            }

            span.frequency {
              position: absolute;
              left: 100%; bottom: 0;
              margin-left: 0.25rem;
              font-size: $fs-16;
              white-space: nowrap;
            }
          }
        }

        p.price-extra {
          margin: ($vmargin/2) 0 0;
          font-size: $fs-15;

          span.amount {font-size: $fs-25;}
          span.vat {display: block; font-size: $fs-15;}
        }
      }

      div.mobile-cta {
        margin-top: $spacing;
        @include bp-m {display: none;}
      }
    }
  }
}

// information

.information {

  // from an article
  &.info-process {
    div.content {
      h3 {
        background: rgba(black,0.2);
        font-size: $fs-22;
      }

      ol {
        margin: 0 0 0 1.25rem;
        padding: ($spacing*0.75) ($spacing/1.5);
        list-style: decimal;
        font-size: $fs-18;
      }
    }
  }

  .content {
    flex: 1;
    &:not([class*="bg-dark"],[class*="bg-b5"]) {background: $clr-4;}

    h2,h3 {
      margin: 0;
      padding: ($spacing*0.75) ($spacing/1.5);
      text-align: center;
      &:not([class*="bg-dark"],[class*="bg-b5"]) {background: $clr-3;}

      &[class*="icon-"]:before {
        display: block;
        margin-bottom: 0.25rem;
        font-size: $fs-35;
        color: $clr-b1;

        @include bp-m {font-size: $fs-40;}

        body.services & {color: $clr-b2;}
        body.resources & {color: $clr-b3;}
        body.associates & {color: $clr-b4;}
        body.toolkit & {color: $clr-b6;}
      }

      span {
        display: block;
        font-size: $fs-18;
        font-weight: 700;
      }
    }
  
    .bg-b5 & {
      background: rgba($clr-2,0.05);
      h3 {background: rgba($clr-2,0.05);}
    }

    .bg-dark & {
      background: rgba(black,0.2);
      h3 {background: rgba(black,0.5);}
    }

    .icon-para & {
      @include bp-m {
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }

    img {
      display: block;
      width: 100%;
    }

    div.text {
      flex: 1;
      padding: $spacing;
      > :last-child {margin-bottom: 0;}
    }

    > p {
      margin: 0;
      padding: $spacing;

      @include bp-m {padding: $spacing*1.5;}

      em { // about page
        display: block;
        margin-bottom: $vmargin/2;
        font: italic $fs-30 Georgia, Serif;
      }

      &[class*="icon-"] {
        display: flex;
        align-items: flex-start;
        font-size: $fs-22;

        &:before {
          flex: 0 1 auto;
          margin-right: 0.3em;
          font-size: $fs-33;
        }
      }

      &.icon-check-circle:before {
        color: $clr-success;
        .bg-dark & {color: white;}
      }
    }
  }
}

// team

ul.team {
  li.col {
    div.content {
      padding: ($spacing*1.25);
      background: $clr-4;

      @include bp-m {
        padding: ($spacing*1.75);

        > div.row > div.col-8 {
          display: flex;
          align-items: center;
        }
      }

      h4 {
        margin: 0;
        text-align: center;

        a.author-linkedin {font-size: $fs-70;}

        span {
          &.name {
            @extend .h3;
          }

          &.title {
            display: block;
            margin-top: 0.25rem;
            font-weight: 400;
            font-size: $fs-20;
            @include bp-m-less {margin-bottom: $vmargin;}
          }
        }
      }
        
      p {
        padding: 0;
        
        a.button.linkedin {
          margin-top: 0.75rem;
          border: 0;
          border-radius: 3rem;
          box-shadow: none;

          &:before {margin: 0;}
          span {@include hide-screen();}
        }
      }
    }
  }
}

// contact buttons

ul.contact-buttons {
  li {
    min-width: 0; // hack to make the email address wrap inside a flex child 

    a.content {
      display: flex;
      align-items: center;
      padding: ($spacing/1.25) $spacing;
      min-width: 0; // hack to make the email address wrap inside a flex child 
      background: $clr-4;
      text-align: left;
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.15s, color 0.15s;

      div.bg-b5 & {background: rgba($clr-2,0.075);}

      div.cta &,
      div.bg-dark & {
        background: rgba(white,0.05);
        color: white;
      }

      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-3;
          color: $clr-b1;
          div.bg-b5 & {background: rgba($clr-2,0.125);}
          div.bg-dark & {background: rgba(white,0.075);}
        }
      }

      &:before {
        margin: 0 0.5rem 0 0;
        font-size: $fs-35;
        color: $clr-b1;
      }

      &[class*="linkedin"]:before {color: #0a66c2;}
      &[class*="youtube"]:before {color: #d70000;}
      &[class*="twitter"]:before {color: #1da1f2;}

      > span {
        @extend .text-wrap;
        min-width: 0; // hack to make the email address wrap inside a flex child 
        
        span {
          @include hide-screen();
        }
      }
    }
  }
}

// contact options

ul.contact-options {
  li {
    position: relative;
    padding-left: 1.5rem;

    &:before {
      position: absolute;
      top: 0.2rem; left: 0;
      font-size: $fs-24;

      footer.footer & {top: 0.05rem;}
    }

    &.email {
      overflow-wrap: break-word;
      word-wrap: break-word;

      span {
        @include bp-s-less {@include hide-screen();}
        footer & {@include hide-screen();}
      }
    }

    &.address {
      div.box &,
      footer & {@include hide-screen();}
      span {display: block;}
    }

    a {
      text-decoration: none;
      
      main & {
        @include bp-m {
          &:hover,
          &:focus {text-decoration: underline;}
        }
      }
    }

    span.icon-angle-right:before {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0.5rem;
      font-size: $fs-10;
    }
  }
}

// call options

div.call-options.general-wrap {

  @include bp-s-less {
    margin-left: -$gutter;
    margin-right: -$gutter;
    border-radius: 0;
  }

  > ul {
    text-align: center;

    input.call-option {
      @include hide-input();

      &:checked {
        ~ label {display: none;}
        ~ div.content {display: flex;}
      }
    }

    > li {
      @include bp-m {min-height: 23rem;}

      label.cta {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: ($spacing*2) $spacing;
        background: $clr-4;
        font-size: $fs-24;
        cursor: pointer;
        transition: background-color 0.15s;

        @include bp-m {font-size: $fs-30;}

        &:before {
          display: block;
          margin-bottom: 1rem;
          font-size: $fs-40;
          color: $clr-b1;

          @include bp-m {font-size: $fs-80;}
        }

        @include bp-m {
          &:hover,
          &:focus {background: $clr-3;}
        }

        b {
          text-decoration: underline;
          text-decoration-thickness: 4px;
          text-underline-offset: 3px;
          text-decoration-color: $clr-b1;
        }
      }

      div.content {
        display: none;
        flex: 1;
        flex-direction: column;
        justify-content: center;
        padding: $spacing;
        background: $clr-1;
        color: white;

        > :last-child {margin-bottom: 0;}

        @include bp-m-less {
          &.call-option-1 {padding: ($spacing*2) $spacing;}
        }

        h2 {
          @extend .h3;

          b {
            text-decoration: underline;
            text-decoration-thickness: 4px;
            text-underline-offset: 3px;
            text-decoration-color: white;
          }
        }

        a {
          color: white;

          @include bp-m {
            &:hover,
            &:focus {text-decoration: none;}
          }
        }

        p.availability {
          font-size: $fs-16;
          span {display: block;}
        }

        p.email {
          font-size: $fs-18;
          span {display: block;}
        }

        p.timezone {
          margin: 0.5rem 0 0;
          font-size: $fs-16;
        }

        div.call-time {
          ol {
            overflow: hidden;
            display: flex;
            margin: 0;
            font-size: $fs-18;

            &.call-hours {border-radius: 0.25rem 0.25rem 0 0;}

            &.call-minutes {
              margin-top: 1px;
              border-radius: 0 0 0.25rem 0.25rem;
            }

            li {
              flex: 1;
              margin-right: 1px;
              background: $clr-2;


              &:last-child {margin-right: 0;}

              input {
                @include hide-input();

                &:checked {
                  ~ label {
                    background: $clr-b1;
                    color: white;
                  }
                }
              }

              label {
                display: block;
                padding: 0.5rem 0.2rem;
                background: rgba(white,0.001);
                cursor: pointer;
                transition: background-color 0.15s;

                @include bp-m {
                  &:hover,
                  &:focus {
                    background: rgba(white,0.05);
                    color: $clr-b1;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

// site map

ul.site-map {
  overflow: hidden;
  border-radius: $border-radius;

  > li {

    a {
      display: block;
      border-bottom: 1px solid white;
      padding: 0.5rem 1rem;
      background: $clr-3;
      text-decoration: none;
      color: $clr-1;

      @include bp-m {
        &:hover,
        &:focus {
          color: $clr-b1;
          box-shadow: inset 0 0 0 100rem rgba($clr-2,0.05);
        }
      }
    }

    ul {
      margin: 0;
      font-size: $fs-18;
      
      li {
        a {
          padding-left: 2rem;
          background: $clr-4;

          em {
            font-weight: 700;
            font-style: normal;
            &:after {content: " - "}
          }
          
          span.snippet b {font-weight: 400;}
        }

        &:last-child a {border-bottom: 0;}
      }
    }
  }
}

// faqs 

ol.faqs {
  margin: 0;
  text-align: left;
  //counter-reset: faq-item;

  li {
    margin: 0 0 4px;
    //counter-increment: faq-item;

    &:last-child {margin-bottom: 0;}

    input {
      @include hide-input();

      + h3 {@extend .h4;}

      + h3, + h4 {
        margin: 0;
        color: $clr-b0; // important for when in a dark background strip
          
        label {
          position: relative;
          display: block;
          margin: 0;
          padding: $spacing;
          padding-right: 3rem;
          background: $clr-4;
          font-weight: 400;
          color: $clr-1;
          transition: background-color 0.15s, color 0.15s;
          cursor: pointer;
          user-select: none;
          -webkit-user-select: none;

          .bg-b5 & {background: rgba($clr-2,0.075);}
          .bg-dark & {background: rgba(black,0.2); color: white;}

          &:before {
            @extend .icon-extension,.icon-plus-circle;
            position: absolute;
            top: 50%; right: 0;
            z-index: 1;
            margin: -0.5rem 1rem 0 0;
            font-size: 1em;
            color: $clr-b1;

            .bg-dark & {color: white;}
          }

          @include bp-m {
            &:hover {
              background: $clr-3;
              color: $clr-b1;

              .bg-b5 & {background: rgba($clr-2,0.125);}
              .bg-dark & {background: rgba(black,0.3);}
            }
          }

          span:before {
            //content: counter(faq-item) ". ";
          }
        }

        + div.answer {
          position: absolute;
          left: -1000em;
          padding: $spacing;
          background: $clr-b5;
          color: $clr-1;
          -webkit-text-size-adjust: 100%; // forcing iOS to stop increasing text size

          div.section.bg-b5 & {background: white;}
          .bg-dark & {background: rgba(white,0.9);}

          :last-child {margin-bottom: 0;}
        }
      }

      &:checked {
        + h3, + h4 {

          label {
            background: $clr-2!important; // prevent hover effect
            color: white;

            .bg-dark & {background: rgba(black,0.5)!important;}

            &:before {
              @extend .icon-minus-circle;
              color: white;
            }
          }

          + div.answer {position: static;}
        }
      }
    }
  }
}

