// articles
// * provides white grid border effect whilst giving the list a background colour 

ol.articles {
  position: relative;
  z-index: 1;

  @include bp-m {background: rgba($clr-4,0.5);}// *

  li.col {
    @include bp-m {box-shadow: 4px 0 0 0 white, 0 4px 0 0 white;}// *

    a {
      display: block;
      background: $clr-4;
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.15s, color 0.15s;

      @include bp-m {
        display: flex;
        flex-direction: column;

        &:hover,
        &:focus {
          background: $clr-3;
          div.text h3 {color: $clr-b1;}
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin-bottom: 0;

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;

        @include bp-m {
          display: flex;
          flex-direction: column;
          flex: 1;
        }

        h3 {
          @extend .h4;
          margin-bottom: $vmargin/2;
          font-weight: 400;
          color: $clr-b0;

          @include bp-m {flex: 0 1 auto;}
        }

        p {
          margin-bottom: 1rem;
          @include bp-m {flex: 1;}
        }

        div.meta {
          font-size: $fs-16;
          @include bp-m {flex: 0 1 auto;}

          img.author {
            overflow: hidden;
            display: inline-block;
            vertical-align: middle;
            margin-right: 0.3rem;
            border-radius: 3rem;
            width: 2.25rem;
            height: 2.25rem;
            box-shadow: 0 0 0 3px $clr-4;
          }

          span {
            display: inline-block;
            vertical-align: middle;
          }

          time {display: block;}
        }
      }
    }
  }
}

// related articles

ul.related-articles {
  li {
    a {
      display: block;
      background: rgba(white,0.75);
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.2s, color 0.2s;

      @include bp-m {
        display: flex;
        align-items: center;

        &:hover,
        &:focus {
          background: $clr-3;
          div.text h3 {color: $clr-b1;}
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin: 0;
        
        @include bp-m {width: 40%;}

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;

        @include bp-m {
          flex: 1;
          padding: ($spacing/2) $spacing;
        }

        h3 {
          margin-bottom: $vmargin/2;
          font-size: $fs-22;
          font-weight: 400;
          color: $clr-b0;
        }

        p {
          margin-bottom: 0;
          $font-size: $fs-18;

          @include bp-m {position: absolute; left: -1000em;}
          @include bp-l {position: static;}
        }
      }
    }
  }
}
