footer.footer {
  background: $clr-4;
  text-align: left;
  font-size: $fs-18;

  body.analytics & {display: none;}
  
  div.site {padding: ($spacing*1.5) 0;}
  
  h2 {
    margin-bottom: ($vmargin/2);
    font-size: $fs-22;

    a {
      color: $clr-1;
      text-decoration: none;

      @include bp-m {
        &:hover,
        &:focus {color: $clr-b1;}
      }
    }

    &.brand {
      display: none;
      margin: 0;
      font-size: $fs-27;
      text-align: center;

      @include bp-m {display: block;}

      b {
        display: inline-block;
        margin: 0 0 0.5rem;
        width: 100%;
        max-width: 330px;
        background: url(../../assets/images/logos/mantra-systems.svg) no-repeat 50% 100%; 
        background-size: 100% auto;

        &:before {
          display: block;
          padding-top: 12%;
          content: "";
        }

        span {@include hide-screen();}
      }

      > span {
        display: block;
        margin: 0.25rem auto 0;
        max-width: 15rem;
      }
    }
  }

  div.compact-form {
    @include bp-m {margin-bottom: 0;}
    form p {margin-bottom: 0;}
  }

  ul,
  p {
    a {
      display: inline-block;
      font-weight: 400;
      color: $clr-1;
      text-decoration: none;

      @include bp-m {
        &:hover,
        &:focus {color: $clr-b1;}
      }
    }
  }

  ul {
    margin: 0;

    li {
      margin: 0.5rem 0;
      @include bp-m {margin: 0.25rem 0;}
    }
  }

  div.company {
    padding: $spacing 0;
    background: $clr-3;
    font-size: $fs-17;
    text-align: center;

    p.cookies,
    ul.links {margin: 0 0 0.5rem;}

    p.location {
      margin-bottom: 0;
      b {display: block;}
      a {display: inline-block;}
    }

    ul {

      li {display: inline-block;}

      &.info li {
        margin: 0;
        &:after {content: ".";}
      }

      &.links {
        margin-bottom: 0.5rem;
        //body.landing & {display: none;}

        li a {
          display: inline-block;
          padding: 0.2rem 0.4rem;
        }
      }
    }
  }
}
